import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from '../global.service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-methods',
    templateUrl: './methods.component.html',
    styleUrls: ['./methods.component.css']
})
export class MethodsComponent implements OnInit {
    public charsLeft: number = 1500;
    methodform: FormGroup;
    Modules: any = [];
    apimethods: any = [];
    user: any = [];
    ModuleName: any;
    MethodName: any;
    MethodType: any;
    Description: any;
    ReqUrl: any;
    ResSam: any;
    checkbox: boolean;
    status: any;
    check: boolean;
    statuscheck: boolean;
    activecheck: boolean;
    deletecheck: boolean;
    lbl: any;
    lblmsg: any;
    action: any;
    ProjectId: any;
    type: any;
    Action: any;
    nav: boolean = true;
    ModuleId: any;
    MethodId: any;
    Methodslength: any;
    Panel1: boolean = JSON.parse(localStorage.getItem('Panel1'));
    Panel2: boolean = JSON.parse(localStorage.getItem('Panel2'));
    UserId: any = JSON.parse(localStorage.getItem('EmployeeId'));
    UserEmail: any = JSON.parse(localStorage.getItem('EmployeeEmail'));
    Username: any = JSON.parse(localStorage.getItem('EmployeeFirstName'));
    UserImage: any = JSON.parse(localStorage.getItem('EmployeeImage'));
    ModuleID: any = JSON.parse(localStorage.getItem('ModuleID'));
    ModuleNAME: any = JSON.parse(localStorage.getItem('ModuleName'));
    P_Name: any = JSON.parse(localStorage.getItem('projectname'));
    constructor(private router: Router, private service: GlobalService, private form: FormBuilder) {
        this.methodform = this.form.group({
            ModuleName: ['', Validators.required],
            MethodName: ['', Validators.required],
            MethodType: ['', Validators.required],
            Description: ['', Validators.required],
            ReqUrl: ['', [Validators.required, Validators.pattern("^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$")]],
            ResSam: ['', Validators.required],
            Checkbox: ''
        })

    }

    ngOnInit() {
        this.Action = 'A';
        this.ProjectId = JSON.parse(localStorage.getItem('projectid'));
        this.ModuleID = JSON.parse(localStorage.getItem('ModuleID'));
        this.ModuleNAME = JSON.parse(localStorage.getItem('ModuleName'));
        this.getmodules();
        this.getallmethods();
        this.action = "add";
    }
    getmodules() {
        var mymodel = { md: { 'mod': this.ProjectId } };
        this.service.getapimodule(mymodel).subscribe(data => {
            this.Modules = data;
        })
    }
    addMethods() {
        this.lblmsg = "";
        this.Panel1 = false;
        this.Panel2 = true;
        this.Action = 'A';
        this.action = "add";
    }
    addParams(method) {
        localStorage.setItem("projectid", JSON.stringify(this.ProjectId));
        localStorage.setItem("projectname", JSON.stringify(this.P_Name));
        localStorage.setItem("moduleid", JSON.stringify(this.ModuleID));
        localStorage.setItem("modulename", JSON.stringify(this.ModuleNAME));
        localStorage.setItem("MethodId", JSON.stringify(method.M_ID));
        localStorage.setItem("Methodname", JSON.stringify(method.M_Name));
        localStorage.setItem('Panel1', JSON.stringify(false));
        localStorage.setItem('Panel2', JSON.stringify(true));
        this.router.navigate(['/ModuleParams']);
    }
    getallmethods() {
        if (this.ModuleID != 0 && this.ModuleID != null && this.ModuleID != "undefined") {
            var mymodel = { md: { 'mod': this.ModuleID } };
            this.service.getapimethod(mymodel).subscribe(data => {
                this.Methodslength = data.length;
                for (let k = 0; k < data.length; k++) {
                    var mymodel = { md: { 'mod': data[k].M_ID } };
                    this.service.getapiparam(mymodel).subscribe(res => {
                        if (k < data.length) {
                            if (data[k].M_ACTIVE == "Y") {
                                this.statuscheck = true;
                                this.activecheck = false;
                                this.deletecheck = false;
                            }
                            if (data[k].M_ACTIVE == "N") {
                                this.statuscheck = false;
                                this.activecheck = true;
                                this.deletecheck = false;
                            }
                            if (data[k].M_ACTIVE == "D") {
                                this.deletecheck = true;
                                this.statuscheck = false;
                                this.activecheck = false;
                            }
                            //else {
                            //    this.statuscheck = false;
                            //}
                        }
                        this.apimethods.push({
                            'M_ID': data[k].M_ID, 'M_MOD_ID': data[k].M_MOD_ID, 'M_NAME': data[k].M_NAME, 'M_TYPE': data[k].M_TYPE, 'M_DESC': data[k].M_DESC,
                            'M_REQUEST_URL_SAMPLE': data[k].M_REQUEST_URL_SAMPLE, 'M_RESPONCE_URL_SAMPLE': data[k].M_RESPONCE_SAMPLE, 'M_ACTIVE': data[k].M_ACTIVE, 'mid': res.length, 'action': this.statuscheck, 'action1': this.activecheck,
                            'action2': this.deletecheck
                        })
                    })
                }

                if (data.length == 0) {
                    this.lblmsg = "No Methods To Display";
                }
            })
        }
        else {
            var count = 0;
            var mymodel = { md: { 'mod': this.ProjectId } };
            this.service.getapimodule(mymodel).subscribe(data => {
                for (let j = 0; j < data.length; j++) {
                    var mymodel1 = { md: { 'mod': data[j].MOD_ID } };
                    this.service.getapimethod(mymodel1).subscribe(res => {
                        count = count + res.length;
                        for (let l = 0; l < res.length; l++) {
                            var mymodel = { md: { 'mod': res[l].M_ID } };
                            this.service.getapiparam(mymodel).subscribe(res1 => {
                        if (l < res.length) {
                            if (res[l].M_ACTIVE == "Y") {
                                this.statuscheck = true;
                                this.activecheck = false;
                                this.deletecheck = false;
                            }
                            if (res[l].M_ACTIVE == "N") {
                                this.statuscheck = false;
                                this.activecheck = true;
                                this.deletecheck = false;
                            }
                            if (res[l].M_ACTIVE == "D") {
                                this.deletecheck = true;
                                this.statuscheck = false;
                                this.activecheck = false;
                            }
                            //else {
                            //    this.statuscheck = false;
                            //}
                        }
                                this.apimethods.push({
                                    'M_ID': res[l].M_ID, 'M_MOD_ID': res[l].M_MOD_ID, 'M_NAME': res[l].M_NAME, 'M_TYPE': res[l].M_TYPE, 'M_DESC': res[l].M_DESC,
                                    'M_REQUEST_URL_SAMPLE': res[l].M_REQUEST_URL_SAMPLE, 'M_RESPONCE_URL_SAMPLE': res[l].M_RESPONCE_SAMPLE, 'mid': res1.length, 'action': this.statuscheck, 'action1': this.activecheck,
                                    'action2': this.deletecheck
                                });
                            })
                        }
                        this.Methodslength = count;
                        if (this.Methodslength == 0) {
                            this.lblmsg = "No Methods To Display";
                        }  
                    })
                }
                if (data.length == 0) {
                    this.lblmsg = "No Methods To Display";
                }          
            })           
        }
    }
    save() {
        this.apimethods = [];
        if (this.methodform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.status = "Y";
                this.MethodId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.MethodId = this.user.M_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            if (this.MethodType == "GET") {
                this.type = "G"
            }
            if (this.MethodType == "POST") {
                this.type = "P"
            }
            var mymodal = { m: { 'action': this.Action, 'M_ID': this.MethodId, 'M_MOD_ID': this.ModuleID, 'M_NAME': this.MethodName, 'M_TYPE': this.type, 'M_DESC': this.Description, 'M_REQUEST_URL_SAMPLE': this.ReqUrl, 'M_RESPONCE_URL_SAMPLE': this.ResSam, 'M_ACTIVE': this.status } };
            this.service.methodsave(mymodal).subscribe(data => {
                this.lbl = data;
                this.getallmethods();
                this.router.navigate(['/Projects']);
            })
            this.clear();
            this.Panel1 = true;
            this.Panel2 = false;
        }
    }
    editmethod(m) {
        var Method = { m };
        this.service.editMethod(Method).subscribe(data => {
            this.user = data[0];
            this.Panel1 = false;
            this.Panel2 = true;
            this.check = true;
            if (data[0].M_ACTIVE == "Y") {
                this.checkbox = true;
            }
            else {
                this.checkbox = false;
            }
            if (data[0].M_TYPE == "G") {
                this.MethodType = "GET";
            }
            else {
                this.MethodType = "POST";
            }
            this.ModuleID = data[0].M_MOD_ID;
            this.MethodName = data[0].M_NAME;
            this.Description = data[0].M_DESC;
            this.ReqUrl = data[0].M_REQUEST_URL_SAMPLE;
            this.ResSam = data[0].M_RESPONCE_URL_SAMPLE;
            this.charsLeft = 1500 - data[0].M_REQUEST_URL_SAMPLE.length;
        })
        this.Action = 'U';
        this.lbl = "";
        this.action = "edit";
    }

    deletemethod(m) {
        this.apimethods = [];
        var Method = { m };
        this.service.deleteMethod(Method).subscribe(data => {
            this.lbl = data;
            this.getallmethods();
            //window.location.reload();
            this.router.navigate(['/Modules']);
        })
    }
    savenaddmethod() {
        if (this.methodform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.status = "Y";
                this.MethodId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.MethodId = this.user.M_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            if (this.MethodType == "GET") {
                this.type = "G"
            }
            if (this.MethodType == "POST") {
                this.type = "P"
            }
            var mymodal = { m: { 'action': this.Action, 'M_ID': this.MethodId, 'M_MOD_ID': this.ModuleID, 'M_NAME': this.MethodName, 'M_TYPE': this.type, 'M_DESC': this.Description, 'M_REQUEST_URL_SAMPLE': this.ReqUrl, 'M_RESPONCE_URL_SAMPLE': this.ResSam, 'M_ACTIVE': this.status } };
            this.service.methodsave(mymodal).subscribe(data => {
                this.lbl = data;
            })
            this.clear();
        }
    }
    savenparam() {
        if (this.methodform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.status = "Y";
                this.MethodId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.MethodId = this.user.M_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            if (this.MethodType == "GET") {
                this.type = "G"
            }
            if (this.MethodType == "POST") {
                this.type = "P"
            }
            var mymodal = { m: { 'action': this.Action, 'M_ID': this.MethodId, 'M_MOD_ID': this.ModuleID, 'M_NAME': this.MethodName, 'MOD_DESC': this.Description, 'M_TYPE': this.type, 'M_DESC': this.Description, 'M_REQUEST_URL_SAMPLE': this.ReqUrl, 'M_RESPONCE_URL_SAMPLE': this.ResSam, 'M_ACTIVE': this.status } };
            this.service.methodsave(mymodal).subscribe(data => {
                this.lbl = data;
                localStorage.setItem("projectid", JSON.stringify(this.ProjectId));
                localStorage.setItem("projectname", JSON.stringify(this.P_Name));
                localStorage.setItem("moduleid", JSON.stringify(this.ModuleID));
                localStorage.setItem("modulename", JSON.stringify(this.ModuleNAME));
                localStorage.setItem("MethodId", JSON.stringify(this.MethodId));
                localStorage.setItem("mname", JSON.stringify(this.MethodName));
                this.router.navigate(['/ModuleParams']);
                localStorage.setItem('Panel1', JSON.stringify(false));
                localStorage.setItem('Panel2', JSON.stringify(true));
            })
            this.clear();
        }
    }
    cancel() {
        this.apimethods = [];
        this.clear();
        this.Panel1 = true;
        this.Panel2 = false;
        this.ngOnInit();
    }
    cancelView() {
        this.router.navigate(['/Projects']);
    }
    clear() {
        this.ModuleName = "undefined";
        this.MethodName = "";
        this.MethodType = "undefined";
        this.Description = "";
        this.ReqUrl = "";
        this.ResSam = "";
        this.checkbox = false;
    }
    backtomod() {
        localStorage.setItem('Panel1', JSON.stringify(true));
        localStorage.setItem('Panel2', JSON.stringify(false));
        localStorage.setItem("P_Name", JSON.stringify(this.P_Name));
        localStorage.setItem("ProjectId", JSON.stringify(this.ProjectId));
        this.router.navigate(['/Modules'])
    }
    viewDetails(method) {
        localStorage.setItem("projectid", JSON.stringify(this.ProjectId));
        localStorage.setItem("projectname", JSON.stringify(this.P_Name));
        localStorage.setItem("moduleid", JSON.stringify(this.ModuleID));
        localStorage.setItem("modulename", JSON.stringify(this.ModuleNAME));
        localStorage.setItem("MethodId", JSON.stringify(method.M_ID));
        localStorage.setItem("mname", JSON.stringify(method.M_NAME));
        localStorage.setItem('Panel1', JSON.stringify(true));
        localStorage.setItem('Panel2', JSON.stringify(false));
        this.router.navigate(['/ModuleParams']);
    }
    statusChange(value: any) {
        this.ModuleID = value;
    }
    logout() {
        localStorage.clear();
        this.router.navigate(['/Login'])
    }
    changed() {
        this.charsLeft = 1500 - this.ReqUrl.length;
    }
    validations() {
        var re = /^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/;
        var count = 0;
        var message = "";
        if (this.ModuleID == null || this.ModuleID == "" || this.ModuleID == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Select Module Name\n";
        }
        if (this.MethodName == null || this.MethodName == "" || this.MethodName == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Method Name\n";
        }
        if (this.MethodType == null || this.MethodType == "" || this.MethodType == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Select Method Type\n";
        }
        if (this.Description == null || this.Description == "" || this.Description == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Description\n";
        }
        if (this.ReqUrl == null || this.ReqUrl == "" || this.ReqUrl == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Request URL Sample\n";
        }
        else if (this.ReqUrl != "" && !re.test(this.ReqUrl)) {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Valid Request URL Sample\n";
        }
        if (this.ResSam == null || this.ResSam == "" || this.ResSam == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Response Sample\n";
        }
        alert(message);
    }
}



