import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from '../global.service';
@Component({
    selector: 'app-dash-board',
    templateUrl: './dash-board.component.html',
    styleUrls: ['./dash-board.component.css']
})
export class DashBoardComponent implements OnInit {
    hideme: any = {};
    projects: any = [];
    prjs: any = [];
    modules: any = [];
    apimodules: any = [];
    apimethods: any = [];
    apiparams: any = [];
    showparams: boolean = false;
    showsidemenu: boolean = false;
    showdata: boolean;
    lblmsg: any;
    M_Name: any;
    projectlogo: any;
    UserId: any = JSON.parse(localStorage.getItem('EmployeeId'));
    UserEmail: any = JSON.parse(localStorage.getItem('EmployeeEmail'));
    UserName: any = JSON.parse(localStorage.getItem('EmployeeFirstName'));
    UserImage: any = JSON.parse(localStorage.getItem('EmployeeImage'));
    constructor(private service: GlobalService, private router: Router) {

    }
    ngOnInit() {
        this.getproject();
        this.getprjs();
    }
    getproject() {
        this.service.getprojects(this.UserId, this.UserEmail).subscribe(data => {
            this.projects = (data[0].Projects);
        })
    }
    getmodules() {
        this.service.getmodules().subscribe(data => {
            this.modules = data;
        })
    }
    getprjs() {
        this.service.getproj().subscribe(data => {
            this.prjs = data;
        })
    }
    statusChange(value: any) {
        var id = +value;
        var mymodal = { md: { 'mod': id } };
        this.service.getapimodule(mymodal).subscribe(data => {
            this.apimodules = data;
            this.showsidemenu = true;
            this.showdata = false;
            this.lblmsg = "";
        })
        var project = { prj: {'P_ID': id }};
        this.service.editproject(project).subscribe(data => {
            this.service.getprojects(this.UserId, this.UserEmail).subscribe(res => {
                for (let i = 0; i < res[0].Projects.length; i++) {
                    if (data[0].P_PEP_P_ID == res[0].Projects[i].Project_Id) {
                        this.projectlogo = res[0].Projects[i].ProjectLogo;
                    }
                }
            })
        })

    }
    getapimethods(value: any) {
        this.lblmsg = "";
        var id = +value;
        var mymodal = { md: { 'mod': id } };
        this.service.getapimethod(mymodal).subscribe(data => {
            this.apimethods = data;
            this.showdata = true;
            this.apiparams = true;
            if (this.apimethods.length==0) {
                this.lblmsg = "No Methods To Display";
            }
        })
    }
    getapiparams(m) {
        var id = +m.M_ID;
        var mymodal = { md: { 'mod': id } };
        this.service.getapiparam(mymodal).subscribe(data => {
            this.apiparams = data
            this.M_Name = m.M_NAME;
        })
        Object.keys(this.hideme).forEach(h => {
            this.hideme[h] = true;
        });
        this.hideme[m.M_ID] = false;
    }
    logout() {
        localStorage.clear();
        this.router.navigate(['/Login'])
    }
}



