import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { GlobalService } from '../global.service';

@Component({
    selector: 'app-module-parameters',
    templateUrl: './module-parameters.component.html',
    styleUrls: ['./module-parameters.component.css']
})
export class ModuleParametersComponent implements OnInit {
    public charsLeft: number = 450;
    Methods: any = [];
    apiparams: any = [];
    user: any = [];
    paramform: FormGroup;
    Panel1: boolean = JSON.parse(localStorage.getItem('Panel1'));
    Panel2: boolean = JSON.parse(localStorage.getItem('Panel2'));
    Method: any;
    ParamId: any;
    Parameter: any;
    DataType: any;
    Description: any;
    ProjectId: any;
    ModuleId: any;
    checkbox: boolean;
    status: any;
    statuscheck: boolean;
    activecheck: boolean;
    deletecheck: boolean;
    check: boolean;
    lbl: any;
    lblmsg: any;
    action: any;
    Action: any;
    Paramslength: any;
    nav: boolean = true;
    UserId: any = JSON.parse(localStorage.getItem('EmployeeId'));
    UserEmail: any = JSON.parse(localStorage.getItem('EmployeeEmail'));
    Username: any = JSON.parse(localStorage.getItem('EmployeeFirstName'));
    UserImage: any = JSON.parse(localStorage.getItem('EmployeeImage'));
    methodid: any = JSON.parse(localStorage.getItem('MethodId'));
    modulename: any = JSON.parse(localStorage.getItem('modulename'));
    P_Name: any = JSON.parse(localStorage.getItem('projectname'));
    name: any = JSON.parse(localStorage.getItem('mname'));
    constructor(private router: Router, private service: GlobalService, private form: FormBuilder) {
        this.paramform = this.form.group({
            Method: ['', Validators.required],
            Parameter: ['', Validators.required],
            DataType: ['', Validators.required],
            Description: ['', Validators.required],
            Checkbox: ''
        })
    }

    ngOnInit() {
        this.Action = 'A';
        this.ProjectId = JSON.parse(localStorage.getItem('projectid'));
        this.ModuleId = JSON.parse(localStorage.getItem('moduleid'));
        this.methodid = JSON.parse(localStorage.getItem('MethodId'));
        this.getmethod();
        this.getallparams();
        this.action = "add";
    }
    addParams() {
        this.lblmsg = "";
        this.Panel1 = false;
        this.Panel2 = true;
        this.Action = 'A';
        this.action = "add";
    }
    getmethod() {
        var mymodel = { md: { 'mod': this.ProjectId } };
        this.service.getapimodule(mymodel).subscribe(data => {
            for (let k = 0; k < data.length; k++) {
                var mymodel = { md: { 'mod': data[k].MOD_ID } };
                this.service.getapimethod(mymodel).subscribe(res => {
                    for (let i = 0; i < res.length; i++) {
                        this.Methods.push({
                            'M_ID': res[i].M_ID, 'M_NAME': res[i].M_NAME
                        });
                    }
                })
            }
        })
    }
    getallparams() {
        if (this.methodid != 0 && this.methodid != null && this.methodid != "undefined") {
            var mymodel = { md: { 'mod': this.methodid } };
            this.service.getapiparam(mymodel).subscribe(data => {
                this.Paramslength = data.length;
                for (let k = 0; k < data.length; k++) {
                    if (k < data.length) {
                        if (data[k].MP_ACTIVE == "Y") {
                            this.statuscheck = true;
                            this.activecheck = false;
                            this.deletecheck = false;
                        }
                        if (data[k].MP_ACTIVE == "N") {
                            this.statuscheck = false;
                            this.activecheck = true;
                            this.deletecheck = false;
                        }
                        if (data[k].MP_ACTIVE == "D") {
                            this.deletecheck = true;
                            this.statuscheck = false;
                            this.activecheck = false;
                        }
                        //else {
                        //    this.statuscheck = false;
                        //}
                    }
                    this.apiparams.push({
                        'MP_ID': data[k].MP_ID, 'MP_M_ID': data[k].MP_M_ID, 'MP_PARAM_NAME': data[k].MP_PARAM_NAME, 'MP_PARAM_DATATYPE': data[k].MP_PARAM_DATATYPE, 'MP_DESC': data[k].MP_DESC,
                        'MP_UID': data[k].MP_UID, 'MP_ACTIVE': data[k].MP_ACTIVE, 'action': this.statuscheck, 'action1': this.activecheck,
                        'action2': this.deletecheck
                    })
                }

                if (data.length == 0) {
                    this.lblmsg = "No Parameters To Display";
                }
            })
        }
        else {
            var count = 0;
            var mymodel = { md: { 'mod': this.ProjectId } };
            this.service.getapimodule(mymodel).subscribe(data => {
                for (let j = 0; j < data.length; j++) {
                    var mymodel1 = { md: { 'mod': data[j].MOD_ID } };
                    this.service.getapimethod(mymodel1).subscribe(res => {
                        for (let i = 0; i < res.length; i++) {
                            var mymodel2 = { md: { 'mod': res[i].M_ID } };
                            this.service.getapiparam(mymodel2).subscribe(res1 => {
                                count = count + res1.length;
                                for (let l = 0; l < res1.length; l++) {
                                if (l < data.length) {
                                    if (res1[l].MP_ACTIVE == "Y") {
                                        this.statuscheck = true;
                                        this.activecheck = false;
                                        this.deletecheck = false;
                                    }
                                    if (res1[l].MP_ACTIVE == "N") {
                                        this.statuscheck = false;
                                        this.activecheck = true;
                                        this.deletecheck = false;
                                    }
                                    if (res1[l].MP_ACTIVE == "D") {
                                        this.deletecheck = true;
                                        this.statuscheck = false;
                                        this.activecheck = false;
                                    }
                                    //else {
                                    //    this.statuscheck = false;
                                    //}
                                }
                                    this.apiparams.push({
                                        'MP_ID': res1[l].MP_ID, 'MP_M_ID': res1[l].MP_M_ID, 'MP_PARAM_NAME': res1[l].MP_PARAM_NAME, 'MP_PARAM_DATATYPE': res1[l].MP_PARAM_DATATYPE, 'MP_DESC': res1[l].MP_DESC,
                                        'MP_UID': res1[l].MP_UID, 'MP_ACTIVE': res1[l].MP_ACTIVE, 'action': this.statuscheck, 'action1': this.activecheck,
                                        'action2': this.deletecheck
                                    });
                                }
                                this.Paramslength = count;
                                if (count == 0) {
                                    this.lblmsg = "No Parameters To Display";
                                }
                            })
                        }
                        if (res.length == 0) {
                            this.lblmsg = "No Parameters To Display";
                        }              
                    })
                } 
                if (data.length == 0) {
                    this.lblmsg = "No Parameters To Display";
                }
            })
        }
    }
    save() {
        this.apiparams = [];
        if (this.paramform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.Action == 'A';
                this.status = "Y";
                this.ParamId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.ParamId = this.user.MP_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            var mymodal = { mp: { 'action': this.Action, 'MP_ID': this.ParamId, 'MP_M_ID': this.methodid, 'MP_PARAM_NAME': this.Parameter, 'MP_PARAM_DATATYPE': this.DataType, 'MP_DESC': this.Description, 'MP_UID': this.UserId, 'MP_ACTIVE': this.status } };
            this.service.paramsave(mymodal).subscribe(data => {
                this.lbl = data;
                this.getallparams();
                this.router.navigate(['/Projects']);
            })
            this.Panel1 = true;
            this.Panel2 = false;
            this.clear();
        }
    }
    editparam(mp) {
        var params = { mp };
        this.service.editParams(params).subscribe(data => {
            this.user = data[0];
            this.Panel1 = false;
            this.Panel2 = true;
            this.check = true;
            if (data[0].MP_ACTIVE == "Y") {
                this.checkbox = true;
            }
            else {
                this.checkbox = false;
            }
            this.methodid = data[0].MP_M_ID;
            this.Parameter = data[0].MP_PARAM_NAME;
            this.DataType = data[0].MP_PARAM_DATATYPE;
            this.Description = data[0].MP_DESC;
            this.charsLeft = 450 - data[0].MP_DESC.length;
        })
        this.Action = 'U';
        this.lbl = "";
        this.action = "edit";
    }

    deleteparam(mp) {
        this.apiparams = [];
        var params = { mp };
        this.service.deleteParams(params).subscribe(data => {
            this.lbl = data;
            this.getallparams();
            //window.location.reload();
            this.router.navigate(['/ModuleParams']);
        })
    }
    savenew() {
        if (this.paramform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.status = "Y";
                this.ParamId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.ParamId = this.user.MP_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            var mymodal = { mp: { 'action': this.Action, 'MP_ID': this.ParamId, 'MP_M_ID': this.methodid, 'MP_PARAM_NAME': this.Parameter, 'MP_PARAM_DATATYPE': this.DataType, 'MP_DESC': this.Description, 'MP_UID': this.UserId, 'MP_ACTIVE': this.status } };
            this.service.paramsave(mymodal).subscribe(data => {
                this.lbl = data;
            })
            this.clear();
        }
    }
    cancel() {
        this.apiparams = [];
        this.clear();
        this.Panel1 = true;
        this.Panel2 = false;
        this.ngOnInit();
    }
    cancelView() {
        this.router.navigate(['/Projects']);
    }
    statusChange(value: any) {
        this.methodid = value;
    }
    statusChange1(value: any) {
    }
    clear() {
        this.Method = "undefined";
        this.Parameter = null;
        this.DataType = "undefined";
        this.Description = null;
        this.checkbox = false;
    }
    logout() {
        localStorage.clear();
        this.router.navigate(['/Login'])
    }
    changed() {
        this.charsLeft = 450 - this.Description.length;
    }
    BackToMethods() {
        localStorage.setItem('Panel1', JSON.stringify(true));
        localStorage.setItem('Panel2', JSON.stringify(false));
        localStorage.setItem("projectid", JSON.stringify(this.ProjectId));
        localStorage.setItem("projectname", JSON.stringify(this.P_Name));
        localStorage.setItem('ModuleID', JSON.stringify(0));
        localStorage.setItem('ModuleName', JSON.stringify(''));
        localStorage.setItem('MethodId', JSON.stringify("undefined"));
        this.router.navigate(['/Methods']); 
    }
    validations() {
        var count = 0;
        var message = "";
        if (this.methodid == null || this.methodid == "" || this.methodid == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Select Method Name\n";
        }
        if (this.Parameter == null || this.Parameter == "" || this.Parameter == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Parameter Name\n";
        }
        if (this.DataType == null || this.DataType == "" || this.DataType == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Select Data Type\n";
        }
        if (this.Description == null || this.Description == "" || this.Description == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Description\n";
        }
        alert(message);
    }

}



