import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { Angular2SocialLoginModule } from 'angular2-social-login';
import { RouterModule, Routes } from '@angular/router';
import { HttpModule, Http } from '@angular/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { GlobalService } from '../app/global.service';
import { CookieService } from 'angular2-cookie/services/cookies.service';
import { OrderModule } from 'ngx-order-pipe';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { DashBoardComponent } from './dash-board/dash-board.component';
import { ProjectsComponent } from './projects/projects.component';
import { ModulesComponent } from './modules/modules.component';
import { ModuleParametersComponent } from './module-parameters/module-parameters.component';
import { MethodsComponent } from './methods/methods.component';

let providers = {
  "google": {
    "clientId": "371137652754-8vo1srvmldbb6j9eqgjq0bk1pd8tnu8u.apps.googleusercontent.com"
  },
  "facebook": {
      "clientId": "950984965051084",
      "apiVersion": "v2.4"
  }
}
const appRoutes: Routes = [
    { path: '', component: LoginComponent },
    { path: 'Login', component: LoginComponent },
    { path: 'DashBoard', component: DashBoardComponent },
    { path: 'Projects', component: ProjectsComponent },
    { path: 'Modules', component: ModulesComponent },
    { path: 'ModuleParams', component: ModuleParametersComponent },
    { path: 'Methods', component: MethodsComponent },
    { path: '**', component: LoginComponent }
];
@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    DashBoardComponent,
    ProjectsComponent,
    ModulesComponent,
    ModuleParametersComponent,
    MethodsComponent
    ],
    imports: [
        BrowserModule, Angular2SocialLoginModule, RouterModule.forRoot(appRoutes), HttpModule, FormsModule, ReactiveFormsModule, OrderModule
    ],
    providers: [GlobalService, CookieService],
    bootstrap: [AppComponent]
})
export class AppModule { }
Angular2SocialLoginModule.loadProvidersScripts(providers);


