import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from '../global.service';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-modules',
    templateUrl: './modules.component.html',
    styleUrls: ['./modules.component.css']
})
export class ModulesComponent implements OnInit {
    public charsLeft: number = 450;
    moduleform: FormGroup;
    modules: any = [];
    prjs: any = [];
    Projects: any = [];
    apimodules: any = [];
    user: any = [];
    apimethods: any = [];
    Action: any;
    Project: any;
    ProjectId: any;
    Module: any;
    Description: any;
    checkbox: boolean;
    status: any;
    lbl: any;
    nav: boolean = true;
    Panel1: boolean = JSON.parse(localStorage.getItem('Panel1'));
    Panel2: boolean = JSON.parse(localStorage.getItem('Panel2'));
    userproject: any = [];
    statuscheck: boolean;
    activecheck: boolean;
    deletecheck: boolean;
    lblmsg: any;
    action: any;
    ModId: any;
    check: any;
    Moduleslength: any;
    UserId: any = JSON.parse(localStorage.getItem('EmployeeId'));
    UserEmail: any = JSON.parse(localStorage.getItem('EmployeeEmail'));
    Username: any = JSON.parse(localStorage.getItem('EmployeeFirstName'));
    UserImage: any = JSON.parse(localStorage.getItem('EmployeeImage'));
    P_ID: any = JSON.parse(localStorage.getItem('ProjectId'));
    P_NAME: any = JSON.parse(localStorage.getItem('P_Name'));
    constructor(private router: Router, private service: GlobalService, private form: FormBuilder) {
        this.moduleform = this.form.group({
            Project: ['', Validators.required],
            Module: ['', Validators.required],
            Description: ['', Validators.required],
            Checkbox: ''
        })
    }

    ngOnInit() {
        this.Action = 'A';
        this.ProjectId = JSON.parse(localStorage.getItem('ProjectId'));
        this.getproject();
        this.getmodules();
        this.getprjs();
        //this.allprojects();
        this.getallmodules();
        this.action="add"
    }
    getproject() {
        this.service.getuserproject(this.UserId, this.UserEmail).subscribe(data => {
            this.userproject = (data[0].ProjectDetais);
        })
    }
    allprojects() {
        var mymodel = { md: { 'mod': this.ProjectId } };
        this.service.getapiproject(mymodel).subscribe(data => {
            this.Projects = data;
        })
    }
    getprjs() {
        this.service.getproj().subscribe(data => {
            this.prjs = data;
        })
    }
    addModules() {
        this.lblmsg = "";
        this.Panel1 = false;
        this.Panel2 = true;
        this.Action = 'A';
        this.action = "add"
    }
    getallmodules() {
        if (this.P_ID != 0) {
            var mymodel = { md: { 'mod': this.P_ID } };
            this.service.getapimodule(mymodel).subscribe(data => {
                this.Moduleslength = data.length;
                for (let k = 0; k < data.length; k++) {
                    var mymodel = { md: { 'mod': data[k].MOD_ID } };
                    this.service.getapimethod(mymodel).subscribe(res => {
                        if (k < data.length) {
                            if (data[k].MOD_ACTIVE == "Y") {
                                this.statuscheck = true;
                                this.activecheck = false;
                                this.deletecheck = false;
                            }
                            if (data[k].MOD_ACTIVE == "N") {
                                this.statuscheck = false;
                                this.activecheck = true;
                                this.deletecheck = false;
                            }
                            if (data[k].MOD_ACTIVE == "D") {
                                this.deletecheck = true;
                                this.statuscheck = false;
                                this.activecheck = false;
                            }
                            //else {
                            //    this.statuscheck = false;
                            //}
                        }
                        this.apimodules.push({
                            'MOD_ID': data[k].MOD_ID, 'MOD_P_ID': data[k].MOD_P_ID, 'MOD_NAME': data[k].MOD_NAME, 'MOD_DESC': data[k].MOD_DESC,
                            'MOD_U_ID': data[k].MOD_U_ID, 'MOD_ACTIVE': data[k].MOD_ACTIVE, 'mid': res.length, 'action': this.statuscheck, 'action1': this.activecheck,
                            'action2': this.deletecheck
                        })
                    })
                }
                //this.apimodules = data;
                if (data.length == 0) {
                    this.lblmsg = "No Modules To Display";
                }
            })
        }
        else {
            this.lblmsg = "Add Module";
        }
    }
    save() {
        this.apimodules = [];
        if (this.moduleform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.status = "Y";
                this.ModId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.ModId = this.user.MOD_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            var mymodal = { mod: { 'action': this.Action, 'MOD_ID': this.ModId, 'MOD_P_ID': this.ProjectId, 'MOD_NAME': this.Module, 'MOD_DESC': this.Description, 'MOD_U_ID': this.UserId, 'MOD_ACTIVE': this.status } };
            this.service.moduleSave(mymodal).subscribe(data => {
                this.lbl = data;
                this.getallmodules();
            })
            this.Panel1 = true;
            this.Panel2 = false;
            this.clear();
        }
    }
    editmodule(mod) {
        var Module = { mod };
        this.service.editModule(Module).subscribe(data => {
            this.user = data[0];
            this.Panel1 = false;
            this.Panel2 = true;
            if (data[0].MOD_ACTIVE == "Y") {
                this.checkbox = true;
            }
            else {
                this.checkbox = false;
            }
            this.ProjectId = data[0].MOD_P_ID;
            this.Module = data[0].MOD_NAME;
            this.Description = data[0].MOD_DESC;
            this.charsLeft = 450 - data[0].MOD_DESC.length;
        })
        this.Action = 'U';
        this.check = true;
        this.lbl = "";
        this.action = "edit"
    }
    deletemodule(mod) {
        this.apimodules = [];
        var Module = { mod };
        this.service.deleteModule(Module).subscribe(data => {
            this.lbl = data;
            this.getallmodules();
            //window.location.reload();
            this.router.navigate(['/Projects']);
        })
    }
    getmodules() {
        this.service.getmodules().subscribe(data => {
            this.modules = data;
        })
    }
    savenmod() {
        if (this.moduleform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.status = "Y";
                this.ModId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.ModId = this.user.MOD_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            var mymodal = { mod: { 'action': this.Action, 'MOD_ID': this.ModId, 'MOD_P_ID': this.ProjectId, 'MOD_NAME': this.Module, 'MOD_DESC': this.Description, 'MOD_U_ID': this.UserId, 'MOD_ACTIVE': this.status } };
            this.service.moduleSave(mymodal).subscribe(data => {
                this.lbl = data;
            })
            this.clear();
        }
    }
    savenmtd() {
        if (this.moduleform.invalid) {
            this.validations();
        }
        else {
            if (this.Action == "A") {
                this.status = "Y";
                this.ModId = 0;
            }
            if (this.Action == "U") {
                this.Action == 'U';
                this.ModId = this.user.MOD_ID;
                if (this.checkbox == true) { this.status = "Y" }
                else { this.status = "N" }
            }
            var mymodal = { mod: { 'action': this.Action, 'MOD_ID': this.ModId, 'MOD_P_ID': this.ProjectId, 'MOD_NAME': this.Module, 'MOD_DESC': this.Description, 'MOD_U_ID': this.UserId, 'MOD_ACTIVE': this.status } };
            this.service.moduleSave(mymodal).subscribe(data => {
                this.lbl = data;
                localStorage.setItem("projectid", JSON.stringify(this.ProjectId));
                localStorage.setItem("projectname", JSON.stringify(this.P_NAME));
                localStorage.setItem("ModuleID", JSON.stringify(this.ModId));
                localStorage.setItem("ModuleName", JSON.stringify(this.Module));
                this.router.navigate(['/Methods']);
                localStorage.setItem('Panel1', JSON.stringify(false));
                localStorage.setItem('Panel2', JSON.stringify(true));
            })
            this.clear();
        }
    }
    statusChange(value: any) {
        this.ProjectId = value;
    }
    cancel() {
        this.clear();
        this.Panel1 = true;
        this.Panel2 = false;
    }
    cancelView() {
        this.router.navigate(['/Projects']);
    }
    clear() {
        this.Project = "undefined";
        this.Module = null;
        this.Description = null;
        this.checkbox = false;
    }
    ViewMethods(m) {
        localStorage.setItem("projectid", JSON.stringify(this.ProjectId));
        localStorage.setItem("projectname", JSON.stringify(this.P_NAME));
        localStorage.setItem("ModuleID", JSON.stringify(m.MOD_ID));
        localStorage.setItem("ModuleName", JSON.stringify(m.MOD_NAME));
        localStorage.setItem('Panel1', JSON.stringify(true));
        localStorage.setItem('Panel2', JSON.stringify(false));
        this.router.navigate(['/Methods']);
    }
    addmethods(m) {
        localStorage.setItem("projectid", JSON.stringify(this.ProjectId));
        localStorage.setItem("projectname", JSON.stringify(this.P_NAME));
        localStorage.setItem("ModuleID", JSON.stringify(m.MOD_ID));
        localStorage.setItem("ModuleName", JSON.stringify(m.MOD_NAME));
        localStorage.setItem('Panel1', JSON.stringify(false));
        localStorage.setItem('Panel2', JSON.stringify(true));
        this.router.navigate(['/Methods'])
    }
    changed() {
        this.charsLeft = 450 - this.Description.length;
    }
    logout() {
        localStorage.clear();
        this.router.navigate(['/Login'])
    }
    validations() {
        var count = 0;
        var message = "";
        if (this.ProjectId == null || this.ProjectId == "" || this.ProjectId == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Select Project\n";
        }
        if (this.Module == null || this.Module == "" || this.Module == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Module Name\n";
        }
        if (this.Description == null || this.Description == "" || this.Description == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Description\n";
        }
        alert(message);
    }
}



