import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalService } from '../global.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
    selector: 'app-projects',
    templateUrl: './projects.component.html',
    styleUrls: ['./projects.component.css']
})
export class ProjectsComponent implements OnInit {
    public myform: FormGroup;
    userproject: any = [];
    Project: any;
    prjs: any = [];
    user: any = [];
    Development: any;
    Staging: any;
    Testing: any;
    Production: any;
    checkbox: boolean;
    check: boolean;
    viewbutton: boolean = true;
    status: any;
    search: any;
    Panel1: boolean = true;
    Panel2: boolean = false;
    Panel3: boolean = false;
    Action: any;
    PrjId: any;
    pid: any;
    p_pid: any;
    pname: any;
    uid: any;
    mod: any;
    sts: any;
    order: any=[];
    name: any;
    projectlength: any;
    showmodulesinfo: boolean;
    counter: number;
    nav: boolean = true;
    searchcancel: boolean= false;
    text: any;
    statuscheck: boolean;
    activecheck: boolean;
    deletecheck: boolean;
    lbl: any = [];
    pepid: any;
    lblmsg: any;
    lblmessage: any;
    action: any;
    apimodules: any = [];
    apimethods: any = [];
    module: any;
    viewlength: any;
    btncancel: boolean = false;
    UserId: any = JSON.parse(localStorage.getItem('EmployeeId'));
    UserEmail: any = JSON.parse(localStorage.getItem('EmployeeEmail'));
    Username: any = JSON.parse(localStorage.getItem('EmployeeFirstName'));
    UserImage: any = JSON.parse(localStorage.getItem('EmployeeImage'));
    constructor(private router: Router, private service: GlobalService, private _fb: FormBuilder) {
        this.myform = this._fb.group({
            Project: ['', Validators.required],
            Development: ['', [Validators.required, Validators.pattern("^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$")]],
            Staging: ['', [Validators.pattern("^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$")]],
            Production: ['', [Validators.pattern("^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$")]],
            Testing: ['', [Validators.pattern("^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$")]],
            Checkbox: '',
        })
        this.counter = 0;
    }

    ngOnInit() {
        this.getproject();
        this.getprj();
        this.action = "add"
        this.service.getproj().subscribe(data => {
            this.projectlength = data.length;
        })
        this.viewbutton = true;
    }
    add() {
        this.clear();
        this.check = false;
        this.Panel1 = false;
        this.Panel2 = true;
        this.Action = 'A';
        this.action="add"
        this.PrjId = 0;
        this.lbl = "";
    }
    save() {
        if (this.myform.invalid) {
            this.validations();
        }
        else {
            this.service.getproj().subscribe(data => {
                for (let i = 0; i < data.length; i++) {
                    if (data[i].P_PEP_P_ID == this.Project) {
                        this.pepid = data[i].P_PEP_P_ID;
                    }
                }
                if (this.pepid == this.Project && this.Action=='A') {
                    this.lblmessage="Project Already Exist!"
                }
                    else {
                        if (this.Action == "A") {
                            this.status = "Y";
                        }
                        if (this.Action == "U") {
                            this.Action == 'U';
                            this.PrjId = this.user.P_ID;
                            if (this.checkbox == true) { this.status = "Y" }
                            else { this.status = "N" }
                        }
                        this.prjs = [];
                        let d = new Date();
                        var mymodal = { prj: { 'action': this.Action, 'P_ID': this.PrjId, 'P_PEP_P_ID': this.Project, 'P_PRJ_NAME': this.name, 'P_API_URL_DEV': this.Development, 'P_API_URL_STAG': this.Staging, 'P_API_URL_PROD': this.Production, 'P_API_URL_TEST': this.Testing, 'P_U_ID': this.UserId, 'P_ACTIVE': this.status, 'P_TS': d } };
                        this.service.dataSave(mymodal).subscribe(data => {
                            this.lbl = data;
                            this.ngOnInit();
                            this.counter = 0;
                            this.router.navigate(['/Projects'])
                        })
                        this.Panel1 = true;
                    this.Panel2 = false;
                    this.lblmessage = "";
                }
            })          
        }
    }
    editpro(prj) {
        var project = { prj };
        this.service.editproject(project).subscribe(data => {
            this.user = data[0];
            this.Panel1 = false;
            this.Panel2 = true;
            this.check = true;
            if (data[0].P_ACTIVE == "Y") {
                this.checkbox = true;
            }
            else {
                this.checkbox = false;
            }
            this.Project =+ data[0].P_PEP_P_ID;
            this.Development = data[0].P_API_URL_DEV;
            this.Staging = data[0].P_API_URL_STAG;
            this.Production = data[0].P_API_URL_PROD;
            this.Testing = data[0].P_API_URL_TEST;
            this.name = data[0].P_PRJ_NAME;
        })
        this.Action = 'U';
        this.lbl = "";
        this.action = "edit"
    }

    deletepro(prj) {
        this.prjs = [];
        var project = { prj };
        this.service.deleteproject(project).subscribe(data => {
            this.lbl = data;
            this.getprj();
            this.counter = 0;
            //window.location.reload();
            this.router.navigate(['/Projects']);
        })
    }

    savenmod() {
        if (this.myform.invalid) {
            this.validations();
        }
        else {
            this.service.getproj().subscribe(data => {
                for (let i = 0; i < data.length; i++) {
                    if (data[i].P_PEP_P_ID == this.Project) {
                        this.pepid = data[i].P_PEP_P_ID;
                    }
                }
                if (this.pepid == this.Project) {
                    this.lblmessage = "Project Already Exist!"
                }
                else {
                    if (this.Action == "A") {
                        this.status = "Y";
                    }
                    if (this.Action == "U") {
                        this.Action == 'U';
                        this.PrjId = this.user.P_ID;
                        if (this.checkbox == true) { this.status = "Y" }
                        else { this.status = "N" }
                    }
                    let d = new Date();
                    var mymodal = { prj: { 'action': this.Action, 'P_ID': this.PrjId, 'P_PEP_P_ID': this.Project, 'P_PRJ_NAME': this.name, 'P_API_URL_DEV': this.Development, 'P_API_URL_STAG': this.Staging, 'P_API_URL_PROD': this.Production, 'P_API_URL_TEST': this.Testing, 'P_U_ID': this.UserId, 'P_ACTIVE': this.status, 'P_TS': d } };
                    this.service.dataSave(mymodal).subscribe(data => {
                        this.lbl = data;
                        this.ngOnInit();
                        localStorage.setItem('ProjectId', JSON.stringify(this.PrjId));
                        localStorage.setItem('P_Name', JSON.stringify(this.name));
                        this.router.navigate(['/Modules']);
                        localStorage.setItem('Panel1', JSON.stringify(false));
                        localStorage.setItem('Panel2', JSON.stringify(true));
                    })
                    this.clear();
                    this.lblmessage = "";
                }
            })
        }
    }
    cancel() {
        this.Panel2 = false;
        this.Panel1 = true;
        this.lblmessage = "";
    }

    getproject() {
        this.service.getuserproject(this.UserId, this.UserEmail).subscribe(data => {
            this.userproject = (data[0].ProjectDetais);
        })
    }
    addmodules(pro) {
        localStorage.setItem('ProjectId', JSON.stringify(pro.P_ID));
        localStorage.setItem('P_Name', JSON.stringify(pro.P_PRJ_NAME));
        localStorage.setItem('Panel1', JSON.stringify(false));
        localStorage.setItem('Panel2', JSON.stringify(true));
        this.router.navigate(['/Modules']);
    }
    viewDetails(pro) {
        localStorage.setItem('ProjectId', JSON.stringify(pro.P_ID));
        localStorage.setItem('P_Name', JSON.stringify(pro.P_PRJ_NAME));
        localStorage.setItem('Panel1', JSON.stringify(true));
        localStorage.setItem('Panel2', JSON.stringify(false));
        this.router.navigate(['/Modules']);
    }
    viewmethods(pro) {
        localStorage.setItem('ModuleID', JSON.stringify("undefined"));
        localStorage.setItem('MethodId', JSON.stringify(0));
        localStorage.setItem('projectid', JSON.stringify(pro.P_ID));
        localStorage.setItem('projectname', JSON.stringify(pro.P_PRJ_NAME));
        localStorage.setItem('ModuleName', JSON.stringify(''));
        localStorage.setItem('Panel1', JSON.stringify(true));
        localStorage.setItem('Panel2', JSON.stringify(false));
        this.router.navigate(['/Methods']);
    }
    addmethods(pro) {
        localStorage.setItem('ModuleID', JSON.stringify("undefined"));
        localStorage.setItem('MethodId', JSON.stringify(0));
        localStorage.setItem('projectid', JSON.stringify(pro.P_ID));
        localStorage.setItem('projectname', JSON.stringify(pro.P_PRJ_NAME));
        localStorage.setItem('ModuleName', JSON.stringify(''));
        localStorage.setItem('Panel1', JSON.stringify(false));
        localStorage.setItem('Panel2', JSON.stringify(true));
        this.router.navigate(['/Methods']);
    }
    viewParams(pro) {
        localStorage.setItem('ModuleID', JSON.stringify(0));
        localStorage.setItem('MethodId', JSON.stringify("undefined"));
        localStorage.setItem('projectid', JSON.stringify(pro.P_ID));
        localStorage.setItem('projectname', JSON.stringify(pro.P_PRJ_NAME));
        localStorage.setItem('modulename', JSON.stringify(''));
        localStorage.setItem('mname', JSON.stringify(''));
        localStorage.setItem('Panel1', JSON.stringify(true));
        localStorage.setItem('Panel2', JSON.stringify(false));
        this.router.navigate(['/ModuleParams']);
    }
    addParams(pro) {
        localStorage.setItem('ModuleID', JSON.stringify(0));
        localStorage.setItem('MethodId', JSON.stringify("undefined"));
        localStorage.setItem('projectid', JSON.stringify(pro.P_ID));
        localStorage.setItem('projectname', JSON.stringify(pro.P_PRJ_NAME));
        localStorage.setItem('modulename', JSON.stringify(''));
        localStorage.setItem('mname', JSON.stringify(''));
        localStorage.setItem('Panel1', JSON.stringify(false));
        localStorage.setItem('Panel2', JSON.stringify(true));
        this.router.navigate(['/ModuleParams']);
    }
    getprj() {
        this.service.getprj().subscribe(data => {
            this.viewlength = data.length;
            if (data.length == 0) {
                this.lblmsg = "No Records To Display!";
            }
            else {
                for (let k = 0; k < data.length; k++) {
                    var mymodel = { md: { 'mod': data[k].P_ID } };
                    this.service.getapimodule(mymodel).subscribe(res => {
                        if (k < data.length) {
                            if (data[k].P_ACTIVE == "Y") {
                                this.statuscheck = true;
                                this.activecheck = false;
                                this.deletecheck = false;
                            }
                            if (data[k].P_ACTIVE == "N") {
                                this.statuscheck = false;
                                this.activecheck = true;
                                this.deletecheck = false;
                            }
                            if (data[k].P_ACTIVE == "D") {
                                this.deletecheck = true;
                                this.statuscheck = false;
                                this.activecheck = false;
                            }
                            //else {
                            //    this.statuscheck = false;
                            //}
                        }
                        this.prjs.push({
                            'P_ID': data[k].P_ID, 'P_PEP_P_ID': data[k].P_PEP_P_ID, 'P_PRJ_NAME': data[k].P_PRJ_NAME, 'P_API_URL_DEV': data[k].P_API_URL_DEV,
                            'P_API_URL_STAG': data[k].P_API_URL_STAG, 'P_API_URL_PROD': data[k].P_API_URL_PROD, 'P_API_URL_TEST': data[k].P_API_URL_TEST,
                            'P_U_ID': data[k].P_U_ID, 'P_ACTIVE': data[k].P_ACTIVE, 'P_TS': data[k].P_TS,'mid': res.length, 'action': this.statuscheck, 'action1': this.activecheck,
                            'action2': this.deletecheck
                        })
                    })
                }
            }
        })
    }

    searchproject() {
        this.lbl = "";
        this.lblmsg = "";
        if (this.search != "" && this.search != "undefined" && this.search != null) {
            var mymodel = { prj: { 'P_PRJ_NAME': this.search } };
            this.service.searchproject(mymodel).subscribe(data => {
                if (data.length == 0) {
                    this.prjs = [];
                    this.lbl = "No Records To Display!";
                }
                else {
                    this.viewbutton = false;
                    this.prjs = [];
                    for (let k = 0; k < data.length; k++) {
                        this.viewlength = data.length;
                        this.projectlength = data.length;
                        var mymodel = { md: { 'mod': data[k].P_ID } };
                        this.service.getapimodule(mymodel).subscribe(res => {
                            if (k < data.length) {
                                if (data[k].P_ACTIVE == "Y") {
                                    this.statuscheck = true;
                                    this.activecheck = false;
                                    this.deletecheck = false;
                                }
                                if (data[k].P_ACTIVE == "N") {
                                    this.statuscheck = false;
                                    this.activecheck = true;
                                    this.deletecheck = false;
                                }
                                if (data[k].P_ACTIVE == "D") {
                                    this.deletecheck = true;
                                    this.statuscheck = false;
                                    this.activecheck = false;
                                }
                                //else {
                                //    this.statuscheck = false;
                                //}
                            }
                            this.prjs.push({
                                'P_ID': data[k].P_ID, 'P_PEP_P_ID': data[k].P_PEP_P_ID, 'P_PRJ_NAME': data[k].P_PRJ_NAME, 'P_API_URL_DEV': data[k].P_API_URL_DEV,
                                'P_API_URL_STAG': data[k].P_API_URL_STAG, 'P_API_URL_PROD': data[k].P_API_URL_PROD, 'P_API_URL_TEST': data[k].P_API_URL_TEST,
                                'P_U_ID': data[k].P_U_ID, 'P_ACTIVE': data[k].P_ACTIVE, 'P_TS': data[k].P_TS, 'mid': res.length, 'action': this.statuscheck, 'action1': this.activecheck,
                                'action2': this.deletecheck
                            })
                        })
                    }
                }
                this.searchcancel = true;
            })
        }    
        //else {
        //    alert("Search Project should not be empty!");
        //}  
    }
    cancelsearch() {
        this.prjs = [];
        this.search = "";
        this.lbl = "";
        this.searchcancel = false;
        this.viewbutton = true;
        this.ngOnInit();
    }
    //getapi(value: any) {
    //    var mymodel = { md: { 'mod': value } };
    //    this.service.getapimodule(mymodel).subscribe(data => {
    //        this.module = data.length;
    //        return (this.module);
    //    })
    //}
    statusChange(value: any) {
        const selectElementText = event.target['options']
        [event.target['options'].selectedIndex].text;
        this.name = selectElementText;
    }
    clear() {
        this.Project = "undefined";
        this.Development = "";
        this.Staging = "";
        this.Production = "";
        this.Testing = "";
    }
    viewmore() {
        this.service.getproj().subscribe(data => {
            for (let i = this.counter + 5; i < data.length; i++) {
                var mymodel = { md: { 'mod': data[i].P_ID } };
                this.service.getapimodule(mymodel).subscribe(res => {
                    if (i < data.length) {
                        if (data[i].P_ACTIVE == "Y") {
                            this.statuscheck = true;
                            this.activecheck = false;
                            this.deletecheck = false;
                        }
                        if (data[i].P_ACTIVE == "N") {
                            this.statuscheck = false;
                            this.activecheck = true;
                            this.deletecheck = false;
                        }
                        if (data[i].P_ACTIVE == "D") {
                            this.deletecheck = true;
                            this.statuscheck = false;
                            this.activecheck = false;
                        }
                        //else {
                        //    this.statuscheck = false;
                        //}
                    }
                    this.prjs.push({
                        'P_ID': data[i].P_ID, 'P_PEP_P_ID': data[i].P_PEP_P_ID, 'P_PRJ_NAME': data[i].P_PRJ_NAME, 'P_API_URL_DEV': data[i].P_API_URL_DEV,
                        'P_API_URL_STAG': data[i].P_API_URL_STAG, 'P_API_URL_PROD': data[i].P_API_URL_PROD, 'P_API_URL_TEST': data[i].P_API_URL_TEST,
                        'P_U_ID': data[i].P_U_ID, 'P_ACTIVE': data[i].P_ACTIVE, 'P_TS': data[i].P_ACTIVE, 'mid': res.length, 'action': this.statuscheck, 'action1': this.activecheck,
                        'action2': this.deletecheck
                    })
                })
                this.viewlength = i + 1;
                if (this.viewlength == this.projectlength) {
                    this.viewbutton = false;
                }
                if (i % 9 == 0) break;
            }
            this.counter += 5;
        });
    }
    cancelcard() {
        this.Panel1 = true;
        this.Panel3 = false;
        this.lblmsg = "";
        this.apimodules = [];
    }
    getmodlen() {
        this.service.getmodlen().subscribe(data => {
            console.log(data);
        })
    }
    viewModules() {
        this.showmodulesinfo = true;
    }
    logout() {
        localStorage.clear();
        this.router.navigate(['/Login'])
    }
    validations() {
        var re = /^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/;
        var message = "";
        var count = 0;
        if (this.Project == "" || this.Project == "undefined" || this.Project == null) {
            count = count + 1;
            message += "(" + count + ")" + " " + "Select Project\n";
        }
        if (this.Development == "" || this.Development == "undefined") {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter Development\n";
        }
        else if (this.Development != "" && !re.test(this.Development)) {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter valid Development URL\n";
        }
        if (this.Staging != "" && !re.test(this.Staging)) {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter valid Staging URL\n";
        }
        if (this.Testing != "" && !re.test(this.Testing)) {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter valid Testing URL\n";
        }
        if (this.Production != "" && !re.test(this.Production)) {
            count = count + 1;
            message += "(" + count + ")" + " " + "Enter valid Production URL\n";
        }
        alert(message);
    }
}



